.profile-menu-wrapper {
  position: absolute;
  right: 0;

  .profile-menu {
    position: relative;
    z-index: 9999;
    background-color: $white;
    border: 1px solid #dbdbdb;
    border-radius: 3px;
    font-weight: 400;
    margin-top: 12px;
    min-width: 180px;
    padding: 5px 15px;

    // .Menu-subcategory {
    //   position: absolute;
    //   left: calc(100% - 20px);
    //   top: -11px;
    //   width: 0;
    //   height: 0;
    //   border: 10px solid transparent;
    //   border-top-width: 0;
    //   border-bottom-color: $primary_600;
    //   background: 0 0;
    //   transform: translateX(-50%);
    //   z-index: 9999;

    //   &::after {
    //     content: "";
    //     width: 0;
    //     height: 0;
    //     top: 1px;
    //     transform: translateX(-50%);
    //     border-bottom-color: $white;
    //     position: absolute;
    //     border-color: transparent;
    //     border-top-width: 10px;
    //     border: transparent solid 10px;
    //     outline: 0;
    //   }
    // }

    ul {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      li {
        padding: 5px 0;
        a {
          color: $grey_900;
          cursor: pointer;

          & :hover {
            color: $wiserr_green;
          }

          & :active {
            color: $wiserr_business;
          }

          & :visited {
            color: $grey_900;
          }
          &:nth-of-type(4) {
            padding-bottom: rem(15px);
          }
        }
        
        &.logout {
          width: 100%;
          border-top: 1px solid $grey_400;
          cursor: pointer;
          padding: 10px 0;
        }
      }
    }
  }
}

.profile-container {
  position: relative;
  z-index: 9999;
}