.header {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: transparent;
  position: fixed;
  z-index: 1500;

  &.nav-details {
    position: static;
    // border-bottom: 1px solid #dadbdd;
    // border-top: 1px solid #dadbdd;
  }

  &.categories-header {
    background-color: $white;

    .top {
      margin: 0;
      background-color: $white;
      -webkit-animation: fade-in 1.2s cubic-bezier(0.445, 0.050, 0.550, 0.950) both;
      animation: fade-in 1.2s cubic-bezier(0.445, 0.050, 0.550, 0.950) both;
    }

    & a {
      color: $grey_900;

      &.seller-nav-link {
        color: $wiserr_business;

      }

      &:hover {
        color: $wiserr_green;
      }
    }

    .logo-search-container {
      .logo {
        svg {
          path:nth-child(1) {
            fill: $grey_1100;
          }
        }
      }
    }
  }


  &.scrolled {
    background-color: $white;

    .nav-link {
      color: $grey_900;

      &.seller-nav-link {
        color: $wiserr_business;

      }

      & :hover {
        color: $wiserr_green;
      }
    }

    .top {
      margin: 0;

      & a {
        color: $grey_900;

        &.seller-nav-link {
          color: #74767E;
        }

        &:hover {
          color: $wiserr_green;
        }
      }


      .logo-search-container {
        .logo {
          svg {
            path:nth-child(1) {
              fill: $grey_1100;
            }
          }
          &.gray {
            svg {
              path:nth-child(1) {
                fill: $grey_1100;
              }
            }
          }
        }
      }
    }
  }
}

.top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: em(25px);
  padding-bottom: em(25px);

  .logo-search-container {
    display: flex;
    align-items: center;

    .hamburger-icon {
      background-color: transparent;
      outline: none;
      border: none;
      color: $white;
      cursor: pointer;

      &::after {
        content: '\f0c9';
        font-family: awesome;
        font-weight: 100;
        font-size: rem(28px);
        letter-spacing: rem(5px);
      }

      &.gray {
        color: $grey_1100;
      }

      @include from-normal-layout {
        display: none;
      }
    }

    .logo {
      // display: none;

      svg {
        path:first-child {
          fill: $white;
        }
      }

      @include from-mobile-layout {
        display: block;
      }
    }
  }
}

.bottom {
  display: flex;
  align-items: center;
  border-top: 1px solid $grey_400;
  border-bottom: 1px solid $grey_400;
  background-color: $white;
  color: $grey_900;
}

@include from-mobile-layout {
  ul {
    &.nav-list {
      display: none;
    }
  }
}

@include from-tablet-layout {
  ul {
    &.nav-list {
      display: flex;
      align-items: center;
      gap: 20px;
      font-size: em(16px);
      font-weight: 600;

      .nav-link {
        color: $white;

        &.open-popup-login:hover {
          color: $wiserr_green;
        }
      }
    }
  }
}

.avatar-img {
  width: 30px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}

.user-msg {
  color: white;
  padding: 10px;
  font-size: 1.4em;
  position: fixed;
  top: 72px;
  right: 0;
  background-color: grey;
  border: 1px solid transparent;
  border-radius: 4px;
  z-index: 9999;
}

.user-msg button {
  float: right;
  border: 0;
  border-radius: 50%;
  margin: 5px;

}

.success {
  background-color: $wiserr_green;
}

.danger {
  background-color: rgb(227, 114, 114);
}

@media (max-width:425px) {
  .header {
    background-color: #fff;
  }

  .top {
    .logo-search-container {
      .logo {
        svg {
          path:nth-child(1) {
            fill: $grey_1000;
          }
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .nav-list {
    display: none;
  }

  .top {
    &.container {
      // padding-inline-start: unset;
      padding-inline-start: rem(10px);
    }
  }
}

